import { LambdaClient, InvokeCommand } from '@aws-sdk/client-lambda';
const region = process.env.REACT_APP_AWS_REGION;
const accessKeyId = process.env.REACT_APP_AWS_ACCESS_KEY_ID;
const secretAccessKey = process.env.REACT_APP_AWS_SECRET_ACCESS_KEY;
const signUpFunction = process.env.REACT_APP_SIGNUP_LAMBDA_ARN;
const lambdaInvocationType = 'RequestResponse';
const awsLogType = 'None';
const clientConfig = {
  region,
  credentials: {
    accessKeyId,
    secretAccessKey,
  },
};

class AWSClient {
  constructor() {
    try {
      this.lambda = new LambdaClient(clientConfig);
      console.debug(`Constructed aws`);
    } catch (e) {}
  }

  async signUp(email) {
    const params = {
      FunctionName: signUpFunction,
      InvocationType: lambdaInvocationType,
      LogType: awsLogType,
      Payload: Buffer.from(
        JSON.stringify({
          email,
        }),
        'utf8'
      ),
    };

    try {
      const data = await this.lambda.send(new InvokeCommand(params));
      const result = JSON.parse(
        JSON.parse(Buffer.from(data.Payload).toString())
      );
      console.debug(`Get response ${result}`);
      return result;
    } catch (err) {
      console.debug(`Get error ${err}`);
      return {
        response: 'failure',
        failureMessage: `Service error. Please try again later.`,
      };
    }
  }
}

const awsClient = new AWSClient();

export default awsClient;
